import {OverlayTrigger, Popover} from "react-bootstrap";
import StatsContext, {Stats} from "./StatsContext";
import PerksContext, {Perks} from "./PerksContext";

type PerkProps = {
    special: string,
    img: string,
    name: string,
    rank: number,
    levels: any[]
}

const PerkElement = ({special, img, name, rank, levels}: PerkProps) => {
    return (
        <StatsContext.Consumer>
            {
                ({getRank, getLevel}: Stats) => {
                    return (
                        <PerksContext.Consumer>
                            {
                                ({perks, perkPointsRemaining}: Perks) => {
                                    return (
                                        <div style={{padding: 10}}>
                                            <OverlayTrigger
                                                trigger={["hover", "focus"]}
                                                placement={"auto"}
                                                transition={false}
                                                overlay={
                                                    <Popover id={name}>
                                                        <Popover.Title>{name}</Popover.Title>
                                                        <Popover.Content>
                                                            {
                                                                levels.map(level => {
                                                                    console.log(level)
                                                                    return (
                                                                        <p key={`${level.name} ${level.rank}`}>
                                                                            Rank {level.rank}
                                                                            {
                                                                                level.level > getLevel() &&
                                                                                <span style={{
                                                                                    fontSize: 12,
                                                                                    fontWeight: 700,
                                                                                    paddingLeft: 4
                                                                                }}>
                                                                                  (Requires Level {level.level})
                                                                                </span>
                                                                            }: {level.description}
                                                                        </p>
                                                                    )
                                                                })
                                                            }
                                                        </Popover.Content>
                                                    </Popover>
                                                }
                                            >
                                                <div
                                                    style={{opacity: getRank(special) >= rank ? 1 : 0.3}}>
                                                    <img
                                                        src={`img/${img}`}
                                                        alt={name}
                                                        style={{
                                                            border: "0px solid #777"
                                                        }}
                                                    />
                                                    <div style={{
                                                        display: "flex",
                                                        justifyContent: "center"
                                                    }}>
                                                        {
                                                            levels.map(level => <PerkStar
                                                                key={`name-${level.rank}`}
                                                                filled={!!perks.find(it => it.name === name && it.rank >= level.rank)}
                                                                enabled={getRank(special) >= rank && getLevel() >= level.level && perkPointsRemaining() > 0}
                                                                special={special}
                                                                name={name}
                                                                rank={level.rank}
                                                                level={level.level}
                                                            />)
                                                        }
                                                    </div>
                                                </div>
                                            </OverlayTrigger>
                                        </div>
                                    )
                                }
                            }
                        </PerksContext.Consumer>
                    )
                }
            }
        </StatsContext.Consumer>
    )
}

type PerkStarProps = {
    filled?: boolean,
    enabled: boolean,
    special: string,
    name: string,
    rank: number,
    level: number
}

const PerkStar = ({filled = false, enabled, special, name, rank, level}: PerkStarProps) => {
    const perk = {special, name, rank, level}
    return (
        <PerksContext.Consumer>
            {
                ({add, remove}: Perks) => {
                    return (
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="16"
                            height="16"
                            fill={filled ? "yellow" : "currentColor"}
                            className="bi bi-star"
                            viewBox="0 0 16 16"
                            onClick={() => enabled && (filled ? remove(perk) : add(perk))}
                            style={{
                                opacity: enabled ? 1 : 0.3,
                                cursor: enabled ? "pointer" : "cursor"
                            }}
                        >
                            <path
                                // style={{fill: "yellow"}}
                                // fill={"yellow"}
                                className={"icon"}
                                d="M2.866 14.85c-.078.444.36.791.746.593l4.39-2.256 4.389 2.256c.386.198.824-.149.746-.592l-.83-4.73 3.522-3.356c.33-.314.16-.888-.282-.95l-4.898-.696L8.465.792a.513.513 0 0 0-.927 0L5.354 5.12l-4.898.696c-.441.062-.612.636-.283.95l3.523 3.356-.83 4.73zm4.905-2.767-3.686 1.894.694-3.957a.565.565 0 0 0-.163-.505L1.71 6.745l4.052-.576a.525.525 0 0 0 .393-.288L8 2.223l1.847 3.658a.525.525 0 0 0 .393.288l4.052.575-2.906 2.77a.565.565 0 0 0-.163.506l.694 3.957-3.686-1.894a.503.503 0 0 0-.461 0z"
                            />
                        </svg>
                    )
                }
            }
        </PerksContext.Consumer>
    )
}

export default PerkElement
